<template>
  <div class="step-wrap">
    <div class="iconfont icon-daosanjiao" :style="{transform: `translateX(${vernierLeft}px)`, color: value.color}"></div>
    <div class="step-item-wrap">
      <span :style="{ backgroundColor: item, border: `1px solid ${item}`, boxShadow: `0 0 20px ${item}`}" v-for="(item, idx) in stepColorList" :key="idx"></span>
    </div>  
  </div>
</template>

<script>
export default {
  name: 'SetpLine',
  props: {
    value: {
      type: Object,
      default: () => {
        return {
          value: '-',
          status: '-',
          color: ''
        }
      }
    }
  },
  watch: {
    value (val) {
      const idx = this.stepColorList.findIndex(item => item === val.color)
      this.vernierLeft = idx > -1 ? (idx * 55) + 15 : 0
    }
  },
  data () { 
    return {
      stepColorList: ['#3E82FF', '#8BF39A', '#ffed5b', '#ff9e5b'],
      vernierLeft: 0 // 游标偏移量
    }
  }
}

</script>
<style scoped lang='scss'>
.step-wrap {
  .icon-daosanjiao {
    margin-bottom: 8px;
    font-size: 22px;
    color: transparent;
    transition: all 0.4s;
  }
  .step-item-wrap {
    display: flex;
    span {
      width: 52px;
      height: 8px;
    }
    span:first-child, span:last-child {
      margin: 0;
    }
  }
  
}
</style>