<template>
  <div class="container">
    <div class="header bg-shadow" v-corner>
      <span class="title">五恒环境监控系统</span>
      <span class="cur-date">{{ curDate }}</span>
    </div>
    <div class="content-wrap">
      <div class="left-wrap">
        <div class="item-wrap bg-shadow" v-corner>
          <div class="desc-title">
            <span class="iconfont icon-wendu"></span>
            <span>&nbsp;温度（℃）</span>
          </div>
          <div class="box-wrap">
            <div class="box" v-for="(item, idx) in temperatureOptions" :key="idx">
              <span class="data">{{ item.value }}</span>
              <span class="label">{{ item.label }}</span>
              <span class="btn" :style="{ backgroundColor: item.color }">{{ item.status }}</span>
            </div>
          </div>
        </div>
        <div class="item-wrap bg-shadow" v-corner>
          <div class="desc-title">
            <span class="iconfont icon-shidu"></span>
            <span>&nbsp;湿度（%RH）</span>
          </div>
          <div class="box-wrap">
            <div class="box" v-for="(item, idx) in humidityOptions" :key="idx">
              <span class="data">{{ item.value }}</span>
              <span class="label">{{ item.label }}</span>
              <span class="btn" :style="{ backgroundColor: item.color }">{{ item.status }}</span>
            </div>
          </div>
        </div>
        <div class="item-wrap bg-shadow" v-corner>
          <div class="desc-title">
            <span class="iconfont icon-tvoc"></span>
            <span>&nbsp;TVOC（mg/m3）</span>
          </div>
          <div class="data-wrap box-wrap">
            <div class="data">
              <strong v-for="(item,idx) in TVOCData.value" :key="idx">{{ item }}</strong>
            </div>
            <StepLine :value="TVOCData"/>
            <div class="desc" :style="{backgroundColor: TVOCData.color}">{{ TVOCData.status }}</div>  
          </div>
        </div>
     
      </div>
      <div class="center-wrap">
        <div class="environment-warp">
          <div class="environment-param" :style="{ background: `linear-gradient(to left, ${item.color}, ${item.opacityColor})` }" v-for="(item, idx) in environmentOptions" :key="idx">
            <span class="label">{{ item.typeName }}<em>{{ item.unit }}</em></span>
            <span class="data">{{ item.value }}</span>
            <span class="desc">{{ item.status }}</span>
          </div>
        </div>
        <div class="cur-pm-wrap bg-shadow" v-corner>
          <div class="cur-pm-title">空气质量检测仪</div>
          <div class="cur-pm-content">
            <div id="step-chart" class="step-chart">
              <StepLineVertical />
            </div>
            <div id="cur-pm-chart" class="cur-pm-chart"></div>
          </div>
        </div>
        <div class="pm-analysis-wrap bg-shadow" v-corner>
          <div class="pm-btn-group">
            <span :class="[curPmBtnActiveIndex === idx && 'active']" @click="handlePmToogle(idx)" v-for="(item, idx) in pmBtnOptions" :key="idx">{{ item }}</span>
          </div>
          <div id="pm"></div>
        </div>
      </div>
      <div class="right-wrap">
        <div class="item-wrap bg-shadow" v-corner>
          <!-- <div class="desc-title">
            <span class="iconfont icon-wendu"></span>
            <span>温度（℃）</span>
          </div>
          <div class="box-wrap">
            <div class="box">
              <span class="data">25.2</span>
              <span class="label">室内温度℃</span>
              <span class="btn">优秀</span>
            </div>
            <div class="box">
              <span class="data">25.2</span>
              <span class="label">室外温度℃</span>
              <span class="btn">优秀</span>
            </div>
          </div> -->
        </div>
        <div class="item-wrap bg-shadow" v-corner>
          <div class="desc-title">
            <span class="iconfont icon-yiyanghuatan"></span>
            <span>&nbsp;CO （PPM）</span>
          </div>
          <div class="data-wrap box-wrap">
            <div class="data">
              <strong v-for="(item,idx) in COData.value" :key="idx">{{ item }}</strong>
            </div>
            <StepLine :value="COData"/>
            <div class="desc" :style="{backgroundColor: COData.color }">{{ COData.status }}</div>   
          </div>
        </div>
        <div class="item-wrap bg-shadow" v-corner>
          <div class="desc-title">
            <span class="iconfont icon-kongqizhiliang"></span>
            <span>&nbsp;室外空气质量</span>
          </div>
          <div class="box-wrap bubble">
            <p class="tem data-wrap">
              <span class="label">温度</span>
              <span class="data">{{ 25.5 || '--' }} <i class="unit">℃</i></span>
            </p>
            <p class="hum data-wrap">
              <span class="label">湿度</span>
              <span class="data">{{ 25.5 || '-- '}} <i class="unit">%RH</i></span>
            </p>
            <p class="pm data-wrap">
              <span class="label">PM2.5</span>
              <span class="data">{{ 25.5 || '--' }} <i class="unit">μg/m3</i></span>
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import tagcloud from '../assets/js/bubble' // 气泡运动库
import dayjs from 'dayjs' // 日期格式化库

import StepLine from '../components/StepLine.vue'
import StepLineVertical from '../components/StepLineVertical.vue'

const ENVIRONMENTDATATYPE = {
  temperature: 'temperature',
  humidity: 'humidity',
  pm: 'pm',
  tvoc: 'tvoc',
  co: 'co'
}

const THEMECOLORCONFIG = {
  color_1: ['rgba(80, 62, 255, 1)', 'rgba(80, 62, 255, 0.4)'],
  color_2: ['rgba(62, 130, 255, 1)', 'rgba(62, 130, 255, 0.4)'],
  color_3: ['rgba(139, 243, 154, 1)', 'rgba(139, 243, 154, 0.4)'],
  color_4: ['rgba(255, 237, 91, 1)', 'rgba(255, 237, 91, 0.4)'],
  color_5: ['rgba(255, 158, 91, 1)', 'rgba(255, 158, 91, 0.4)']
}

const CRITICALCONFIGLIST = [
  {
    type: ENVIRONMENTDATATYPE.temperature,
    criticalList: [
      {
        color: THEMECOLORCONFIG.color_1[0],
        opacityColor: THEMECOLORCONFIG.color_1[1], // 中间顶部数据要用，渐变色
        status: '寒冷',
        min: 'infinite',
        max: 16
      },
      {
        color: THEMECOLORCONFIG.color_2[0],
        opacityColor: THEMECOLORCONFIG.color_2[1],
        status: '冷',
        min: 16,
        max: 20
      },
      {
        color: THEMECOLORCONFIG.color_3[0],
        opacityColor: THEMECOLORCONFIG.color_3[1],
        status: '舒适',
        min: 20,
        max: 26
      },
      {
        color: THEMECOLORCONFIG.color_4[0],
        opacityColor: THEMECOLORCONFIG.color_4[1],
        status: '热',
        min: 26,
        max: 30
      },
      {
        color: THEMECOLORCONFIG.color_5[0],
        opacityColor: THEMECOLORCONFIG.color_5[1],
        status: '炎热',
        min: 30,
        max: 'infinite'
      }
    ]
  },
  {
    type: ENVIRONMENTDATATYPE.humidity,
    criticalList: [
      {
        color: THEMECOLORCONFIG.color_1[0],
        opacityColor: THEMECOLORCONFIG.color_1[1], 
        status: '干燥',
        min: 'infinite',
        max: 30
      },
      {
        color: THEMECOLORCONFIG.color_2[0],
        opacityColor: THEMECOLORCONFIG.color_2[1], 
        status: '微干',
        min: 30,
        max: 40
      },
      {
        color: THEMECOLORCONFIG.color_3[0],
        opacityColor: THEMECOLORCONFIG.color_3[1], 
        status: '舒适',
        min: 40,
        max: 65
      },
      {
        color: THEMECOLORCONFIG.color_4[0],
        opacityColor: THEMECOLORCONFIG.color_4[1], 
        status: '微湿',
        min: 65,
        max: 75
      },
      {
        color: THEMECOLORCONFIG.color_5[0],
        opacityColor: THEMECOLORCONFIG.color_5[1], 
        status: '潮湿',
        min: 75,
        max: 'infinite'
      },
    ]
  },
  {
    type: ENVIRONMENTDATATYPE.tvoc,
    criticalList: [
      {
        color: THEMECOLORCONFIG.color_2[0],
        opacityColor: THEMECOLORCONFIG.color_2[1], 
        status: '优',
        min: 'infinite',
        max: 0.3
      },
      {
        color: THEMECOLORCONFIG.color_3[0],
        opacityColor: THEMECOLORCONFIG.color_3[1], 
        status: '良',
        min: 0.3,
        max: 1
      },
      {
        color: THEMECOLORCONFIG.color_4[0],
        opacityColor: THEMECOLORCONFIG.color_4[1], 
        status: '差',
        min: 1,
        max: 3
      },
      {
        color: THEMECOLORCONFIG.color_5[0],
        opacityColor: THEMECOLORCONFIG.color_5[1], 
        status: '极差',
        min: 3,
        max: 'infinite'
      },
    ]
  },
  {
    type: ENVIRONMENTDATATYPE.pm,
    criticalList: [
      {
        color: THEMECOLORCONFIG.color_2[0],
        opacityColor: THEMECOLORCONFIG.color_2[1], 
        status: '优',
        min: 'infinite',
        max: 50
      },
      {
        color: THEMECOLORCONFIG.color_3[0],
        opacityColor: THEMECOLORCONFIG.color_3[1], 
        status: '良',
        min: 50,
        max: 100
      },
      {
        color: THEMECOLORCONFIG.color_4[0],
        opacityColor: THEMECOLORCONFIG.color_4[1], 
        status: '差',
        min: 100,
        max: 200
      },
      {
        color: THEMECOLORCONFIG.color_5[0],
        opacityColor: THEMECOLORCONFIG.color_5[1], 
        status: '极差',
        min: 200,
        max: 'infinite'
      },
    ]
  },
  {
    type: ENVIRONMENTDATATYPE.co,
    criticalList: [
      {
        color: THEMECOLORCONFIG.color_2[0],
        opacityColor: THEMECOLORCONFIG.color_2[1], 
        status: '优',
        min: 'infinite',
        max: 450
      },
      {
        color: THEMECOLORCONFIG.color_3[0],
        opacityColor: THEMECOLORCONFIG.color_3[1], 
        status: '良',
        min: 450,
        max: 1000
      },
      {
        color: THEMECOLORCONFIG.color_4[0],
        opacityColor: THEMECOLORCONFIG.color_4[1], 
        status: '差',
        min: 1000,
        max: 2000
      },
      {
        color: THEMECOLORCONFIG.color_5[0],
        opacityColor: THEMECOLORCONFIG.color_5[1], 
        status: '极差',
        min: 2000,
        max: 'infinite'
      }
    ]
  }
]

export default {
  name: '',
  directives: {
    corner(el, binding) {
      const corner = Array.from(el.getElementsByClassName('corner-span'))
      if(corner.length) return ; //时间定时器导致指令一直执行
      el.style.position = binding.value || 'relative'
      const spanWidth = 10;
      const lineList = [
        {
          top: 0,
          left: 0,
          rotate: 0,
        },
        {
          top: spanWidth / 2,
          left: -spanWidth / 2,
          rotate: 90,
        },
        {
          top: 0,
          right: 0,
          rotate: 0,
        },
        {
          top: spanWidth / 2,
          right: -spanWidth / 2,
          rotate: 90,
        },
        {
          bottom: 0,
          left: 0,
          rotate: 0,
        },
        {
          bottom: spanWidth / 2,
          left: -spanWidth / 2,
          rotate: -90,
        },
        {
          bottom: 0,
          right: 0,
          rotate: 0,
        },
        {
          bottom: spanWidth / 2,
          right: -spanWidth / 2,
          rotate: 90,
        },
      ];
      lineList.forEach((item) => {
        const span = document.createElement("span");
        span.className = 'corner-span'
        span.style.position = "absolute";
        span.style.left = item.left + "px";
        span.style.right = item.right + "px";
        span.style.top = item.top + "px";
        span.style.bottom = item.bottom + "px";
        span.style.transform = `rotate(${item.rotate}deg)`;
        span.style.width = `${spanWidth}px`;
        span.style.height = "1px";
        span.style.background = "cyan";
        el.append(span);
      });
    },
  },
  components: {
    StepLine,
    StepLineVertical
  },
  mounted () {
    window.onload = () => {
      this.getCurDate() // 获取当前时间
      this.pmChartInit() // 底部pm2.5走势图
      this.bubbleInit() // 右下角气泡初始化
      this.curPmChartInit() // 当前pm2.5数据

      this.mockData() // 模拟数据，后续删除
    }

    // 屏幕分辨率改变执行图标resize事件
    window.onresize = () => {
      this.chartResize()
    }
  },
  // 组件卸载停止监听窗口大小事件
  destroyed () {
    window.onresize = null
    this.dateTimer = null
    this.resizeTimer = null
  },
  data () {
    return {
      pmChart: null, // pm2.5的曲线走势图
      resizeTimer: null, // 窗口大小防抖定时器
      dateTimer: null, // 当前时间定时器
      // 所有环境数据配置
      environmentOptions: {
        temperature: {
          typeName: '温度',
          unit: ' ℃',
          value: '-',
          status: '-',
          color: '',
          style: {
            background: 'linear-gradient(to left, #503EFF, #503eff66)'
          }
        },
        humidity: {
          typeName: '湿度',
          unit: ' %RH',
          value: '-',
          status: '-',
          color: '',
          style: {
            background: 'linear-gradient(to left, #3E82FF, #3e82ff66)'
          }
        },
        pm: {
          typeName: 'PM2.5',
          unit: ' μg/m³',
          value: '-',
          status: '-',
          color: '',
          style: {
            background: 'linear-gradient(to left, #8BF39A, #8bf39a66)'
          }
        },
        co: {
          typeName: 'CO',
          unit: ' ppm',
          value: '-',
          status: '-',
          color: '',
          style: {
            background: 'linear-gradient(to left, #ffed5b, #ffed5b66)'
          }
        },
        tvoc: {
          typeName: 'TVOC',
          unit: ' mg/m3',
          value: '-',
          status: '-',
          color: '',
          style: {
            background: 'linear-gradient(to left, #ff9e5b, #ff9e5b66)'
          }
        }
      },
      // 左侧温度数据配置
      temperatureOptions:{
        indoor: {
          value: '-',
          label: '室内温度 ℃',
          status: '-',
          color: ''
        },
        outdoor: {
          value: '-',
          label: '室外温度 ℃',
          status: '-',
          color: ''
        }
      },
      // 左侧湿度数据配置
      humidityOptions:{
        indoor: {
          value: '-',
          label: '室内湿度 %R',
          status: '-',
          color: ''
        },
        outdoor: {
          value: '-',
          label: '室外湿度 %RH',
          status: '-',
          color: ''
        }
      },
      // pm2.5数据配置
      pmData: {
        value: '-',
        status: '-',
        color: ''
      },
      // tvoc 数据配置
      TVOCData: {
        value: '-',
        status: '-',
        color: ''
      },
      // CO 数据配置
      COData: {
        value: '-',
        status: '-',
        color: ''
      },
      curDate: dayjs(new Date()).format("YYYY年MM月DD日 hh:mm:ss"),
      pmBtnOptions: ['今天', '近一周', '近一月'],
      curPmBtnActiveIndex: 0, // 当前选择数据范围下标
      curPmChart: null // 中间当前pm2.5的图表
    }
  },
  methods: {
    // 模拟数据
    mockData () {
      // 左侧温度数据
      const indoorTemperature = Math.ceil(Math.random() * 40)
      const outdoorTemperature = Math.ceil(Math.random() * 40)
      this.setStackEnvValue('temperatureOptions', 'indoor', indoorTemperature, ENVIRONMENTDATATYPE.temperature)
      this.setStackEnvValue('temperatureOptions', 'outdoor', outdoorTemperature, ENVIRONMENTDATATYPE.temperature)

      // 左侧湿度数据
      const indoorHumidity = Math.ceil(Math.random() * 100)
      const outdoorHumidity = Math.ceil(Math.random() * 100)
      this.setStackEnvValue('humidityOptions', 'indoor', indoorHumidity, ENVIRONMENTDATATYPE.humidity)
      this.setStackEnvValue('humidityOptions', 'outdoor', outdoorHumidity, ENVIRONMENTDATATYPE.humidity)

      // 左侧tvoc数据
      const tvocData = (Math.random() * 9).toFixed(2)
      this.setEnvValue('TVOCData', tvocData, ENVIRONMENTDATATYPE.tvoc)

      // 右侧一氧化碳数据
      const coData = (Math.random() * 2000).toFixed(2)
      this.setEnvValue('COData', coData, ENVIRONMENTDATATYPE.co)

      // 当前pm2.5数据
      const pmData = (Math.random() * 300).toFixed(2)
      this.setEnvValue('pmData', pmData, ENVIRONMENTDATATYPE.pm)
      this.renderCurPmChart()

      // 中间顶部数据
      this.setStackEnvValue('environmentOptions', 'temperature', indoorTemperature, ENVIRONMENTDATATYPE.temperature)
      this.setStackEnvValue('environmentOptions', 'humidity', indoorHumidity, ENVIRONMENTDATATYPE.humidity)
      this.setStackEnvValue('environmentOptions', 'pm', pmData, ENVIRONMENTDATATYPE.pm)
      this.setStackEnvValue('environmentOptions', 'co', coData, ENVIRONMENTDATATYPE.co)
      this.setStackEnvValue('environmentOptions', 'tvoc', tvocData, ENVIRONMENTDATATYPE.tvoc)
    },
    setEnvValue (dataKey, value, type) {
      this[dataKey] = {
        value,
        ...this.getEnvStatus(value, type)
      }
    },
    setStackEnvValue (dataKey, key, value, type) {
      this[dataKey][key] = {
        ...this[dataKey][key],
        value,
        ...this.getEnvStatus(value, type)
      }
    },
    // 根据数值选择颜色和状态值 value 当前值  type 什么类型值的回显
    getEnvStatus (value, type) {
      const [{ criticalList = [] } = {}] = CRITICALCONFIGLIST.filter(item => type === item.type)
      let firstValue = criticalList[0]
      let lastValue = criticalList[criticalList.length - 1]
      for (let item of criticalList) {
        // 小于边界值
        if (item.min === 'infinite' && value <= item.max) {
          return {
            color: firstValue.color,
            status: firstValue.status,
            opacityColor: firstValue.opacityColor
          }
        }
        // 大于边界值
        if (item.max === 'infinite' && value >= item.min) {
          return {
            color: lastValue.color,
            status: lastValue.status,
            opacityColor: lastValue.opacityColor
          }
        }
        if (value > item.min && value <= item.max) {
          return {
            color: item.color,
            status: item.status,
            opacityColor: item.opacityColor
          }
        }
      }
      return {}
    },
    // 获取当前日期
    getCurDate() {
      this.dateTimer = setInterval(() => {
        const curDate = new Date()
        this.curDate = dayjs(curDate).format(
          "YYYY年MM月DD日 HH:mm:ss"
        )
      }, 1000)
    },
    // 图表重置大小
    chartResize () {
      this.resizeTimer && clearTimeout(this.resizeTimer)
      this.resizeTimer = setTimeout(() => {
        this.pmChart.resize()
        this.curPmChart.resize()
      }, 500)
    },
    bubbleInit () {
      /*3D标签云*/
      tagcloud({
        selector: ".bubble",  //元素选择器
        fontsize: 6,       //基本字体大小, 单位px
        radius: 60,         //滚动半径, 单位px 页面宽度和高度的五分之一
        mspeed: "slow",   //滚动最大速度, 取值: slow, normal(默认), fast
        ispeed: "slow",   //滚动初速度, 取值: slow, normal(默认), fast
        direction: 10,     //初始滚动方向, 取值角度(顺时针360): 0对应top, 90对应left, 135对应right-bottom(默认)...
        keep: false          //鼠标移出组件后是否继续随鼠标滚动, 取值: false, true(默认) 对应 减速至初速度滚动, 随鼠标滚动
      });
    },
    // 初始化pm2.5图像
    pmChartInit () {
      this.pmChart = this.$echarts.init(document.getElementById('pm'));
      this.handlePmToogle(0)
    },
    // pm2.5的走势图
    renderPmChart({xAxisData = [], yAxisData = [], yAxisStackData = []} = {}) {
      this.pmChart.setOption({
        title: {
          text: 'PM2.5',
          // textVerticalAlign:'middle',
          top: 0,
          right: "4%",
          textStyle: {
            fontSize: 11,
            color: "#1a88b8",
          },
        },
        tooltip: {
          trigger: "axis",
          backgroundColor:'rgba(7,45,106,0.8)',
          borderWidth:0,
          textStyle:{
            fontSize:12,
            color:"#5dbbfa",
          },
          formatter:function(params) {
            let str = `<span style="color:#fff">${params[0].axisValue}</span></br>`
            for(let i = 0;i < params.length; i ++) {
              str += `${params[i].marker}${(params[i].data === null ? '--' : params[i].data)}μg/m³</br>`
            }
            return str;
          }
        },
        xAxis: {
          type: "category",
          data: xAxisData,
          axisLabel: {
            color: "#1a88b8",
            fontSize: 10,
          },
          axisLine: {
            lineStyle: {
              color: "#105893",
            },
          },
          axisTick: {
            show: true,
            inside: true,
          }
        },
        yAxis: {
          type: "value",
          name: "μg/m³",
          nameLocation: "end",
          nameTextStyle: {
            fontSize: 10,
            verticalAlign: "middle",
            color: "#1b77ac",
          },
          axisLabel: {
            color: "#1a88b8",
            fontSize: 10,
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: "#105893",
            },
          },
          axisTick: {
            show: true,
            inside: true,
          },
          splitLine: {
            lineStyle: {
              color: "#105893",
            },
          },
        },
        grid: {
          left: "4%",
          top: "12%",
          bottom: "18%",
          right: "4%",
        },
        legend: {
          show: yAxisStackData.length,
          textStyle:{
            color:'#fff',
            fontSize:12
          },
          itemWidth:12,
          itemHeight:12,
          left:'10%',
          top: -2,
          animation: false
        },
        series: [
          {
            data: yAxisData,
            type: "line",
            stack: 'pm',
            name: '中午',
            lineStyle: {
              width: 0,
            },
            showSymbol: true,
            // symbol:'circle',
            itemStyle: {
              color: "rgba(92, 164, 199,0.6)",
              borderColor: "rgb(92, 164, 199)",
            },
            areaStyle: {
              opacity: 0.8,
              color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: "rgba(92, 164, 199,1)",
                },
                {
                  offset: 1,
                  color: "rgba(92, 164, 199,0.4)",
                },
              ]),
            },
            smooth: true,
          },
          {
            data: yAxisStackData,
            type: "line",
            stack: 'pm',
            name: '晚上',
            lineStyle: {
              width: 0,
            },
            showSymbol: true,
            // symbol:'circle',
            itemStyle: {
              color: "rgba(190, 93, 104,0.7)",
              borderColor: "rgb(190, 93, 104)",
            },
            areaStyle: {
              opacity: 0.8,
              color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: "rgb(190, 93, 104)",
                },
                {
                  offset: 1,
                  color: "rgba(190, 93, 104, 0.4)",
                },
              ]),
            },
            smooth: true,
          }
        ]
      })
    },
    // 切换pm2.5的数据范围
    handlePmToogle (idx) {
      this.curPmBtnActiveIndex = idx
      // 今天
      if (idx === 0) {
        const yAxisData = []
        const xAxisData = []
        for(let i = 0; i < 24; i++) {
          yAxisData.push((Math.random() * 300).toFixed(2)) 
          xAxisData.push(`00:${i < 10 ? '0' + i : i}`)
        }
        this.renderPmChart({xAxisData, yAxisData})
      }
      // 近一周
      if (idx === 1) {
        const yAxisData = []
        const xAxisData = []
        const yAxisStackData = []
        for(let i = 0; i < 7; i++) {
          yAxisData.push((Math.random() * 300).toFixed(2))
          yAxisStackData.push((Math.random() * 300).toFixed(2))
          xAxisData.push(`03-${i < 10 ? '0' + (i + 1): i}`)
        }
        this.renderPmChart({xAxisData, yAxisData, yAxisStackData})
      }
      // 近一月
      if (idx === 2) {
        const yAxisData = []
        const xAxisData = []
        for(let i = 0; i < 30; i++) {
          yAxisData.push((Math.random() * 300).toFixed(2))
          xAxisData.push(`03-${i < 10 ? '0' + (i + 1): i}`)
        }
        this.renderPmChart({xAxisData, yAxisData})
      }
    },
    // 中间当前pm2.5的数据展示
    curPmChartInit () {
      this.curPmChart = this.$echarts.init(document.getElementById('cur-pm-chart'));
      this.renderCurPmChart()
    },
    // 渲染中间当前pm2.5的数据
    renderCurPmChart () {
      let { status: statusText, color:statusColor, value: statusValue } = this.pmData
      this.curPmChart.setOption({
        title: {
          text: statusValue,
          x: '50%',
          y: '53%',
          textAlign: 'center',
          textStyle: {
            fontWeight: '500',
            color: statusColor,
            fontFamily: '微软雅黑',
            fontSize: 75,
          },
        },
        series: [
          // 外边刻度配置
          // {
          //   type: 'gauge',
          //   radius: '150%', // 1行3个
          //   center: ['50%', '85%'],
          //   splitNumber: 4,
          //   // min: 0,
          //   // max: 200,
          //   startAngle: 180,
          //   endAngle: 0,
          //   z:99,
          //   // 线
          //   axisLine: {
          //     lineStyle: {
          //       width: 1,
          //       color: [[1, 'rgba(255,255,255,0)']],
          //     },
          //     detail: {
          //       formatter: '{value}',
          //     },
          //     data: [
          //       {
          //         value: 50,
          //         name: 'SCORE',
          //       }
          //     ]
          //   },
          //   // 刻度线文字
          //   axisLabel: {
          //     show: false,
          //     color: '#02C2A2',
          //     fontSize: 16,
          //     distance: -30,
          //   },
          //   // 刻度标签。
          //   axisTick: {
          //     show: true,
          //     splitNumber: 10, //刻度的段落数
          //     lineStyle: {
          //       color: '#013c72',
          //       width: 10, //刻度的宽度
          //       shadowColor: '#503eff66',
          //       shadowBlur: 20,
          //     },
          //     length: 16 //刻度的长度
          //   },
          //   // 当前数据
          //   data: [
          //     {
          //       value: demoData.value,
          //       name: '', // 
          //       itemStyle: {
          //         color: '#02C3A2',
          //       },
          //     }
          //   ],
          //   // 指针
          //   pointer: {
          //     show: true,
          //     length: '12%',
          //     radius: '50%',
          //     width: 10, //指针粗细
          //     offsetCenter:[0,-210]
          //   },
          //   // 默认值原有值的位置
          //   detail: {
          //     show:false
          //   }
          // },
          {
            type: 'gauge',
            z: 12,
            radius: '145%', // 1行3个
            center: ['50%', '85%'],
            startAngle: 180,
            endAngle: 0,
            min: 0,
            max: 200,
            splitNumber: 70,
            splitLine: {
              show: true,
              length: 16,
              distance: -16,
              lineStyle: {
                color: '#034b8a',
                width: 6
              }
            },
            // 仪表盘的线，颜色值为一个数组
            axisLine: {
              show: true,
              // 两端是否设置为圆角；在5.0之后的版本有效
              roundCap: false,
              lineStyle: {
                width: 16,
                // shadowColor: '#0247C7',
                // shadowBlur: 20,
                color: [
                  [
                    statusValue / 200,
                    {
                      x: 0,
                      y: 0,
                      x1: 1,
                      y1: 0,
                      colorStops: [
                        {
                          offset: 0,
                          color: statusColor || '#0247C7',
                        },
                        {
                          offset: 1,
                          color: statusColor || '#0247C7',
                        }
                      ]
                    }
                  ],
                  [1, 'rgba(2, 71, 199, 0.15)'],
                ]
              }
            },
            // 仪表盘刻度标签
            axisLabel: {
              show: false,
            },
            // 刻度
            axisTick: {
              show: false,
            },
            // 指针
            pointer: {
              show: true,
              length: '12%',
              radius: '50%',
              width: 10, //指针粗细
              offsetCenter:[0, '-80%']
            },
            data: [
              {
                value: statusValue,
                itemStyle: {
                  color: statusColor // 指针颜色
                }
              }
            ],
            detail: {
              //标题
              show: true,
              offsetCenter: [0, '-55%'],
              color: '#FFFFFF',
              fontSize: 12,
              formatter: function() {
                return '室内PM2.5（μg/m3）'
              }
            }
          },
          // 内层弧度配置
          {
            name: '空气质量检测',
            type: 'pie',
            radius: ['115%', '130%'], // 弧度半径
            center: ['50%', '85%'], // 弧度原点
            startAngle: 180, // 起始弧度
            endAngle: 0, // 结束弧度
            color: [
              {
                type: 'linear',
                x: 1,
                y: 0,
                x2: 0,
                y2: 0,
                colorStops: [
                  // {
                  //   offset: 0,
                  //   color: '#1c619a', // 0% 处的颜色
                  // },
                  // { 
                  //   offset: 0.5,
                  //   color: '#8bf39a66', // 50% 处的颜色
                  // },
                  {
                    offset: 1,
                    color: '#1c619a' // 100% 处的颜色
                  }   
                ]
              },
              'transparent'
            ],
            hoverAnimation: true,
            legendHoverLink: false,
            z: 10,
            label: {
              formatter () {
                return statusText
              },
              offset: [0, -30],
              color: '#ffffff',
              fontSize: 14,
              borderColor: statusColor, // 值域边框颜色
              backgroundColor: statusColor,
              //边框颜色
              borderWidth: 1,
              borderRadius: 2,
              padding: [3, 18, 3, 18],
              position: 'center'
            },
            labelLine: {
              show: false
            },
            data: [100, 100]
          },
          // 最内层半圆
          {
            type: 'pie',
            hoverAnimation: false,
            label: {
              show: false,
            },
            center: ['50%', '85%'],
            radius: ['0%', '105%'],
            startAngle: 180,
            data: [
              {
                value: 100,
                itemStyle: {
                  color: {
                    type: 'radial',
                    x: 0.5,
                    y: 0.5,
                    r: 0.5,
                    colorStops: [
                      { offset: 0, color: '#00C2EB' },
                      { offset: 1, color: '#094461' }
                    ]
                  },
                  opacity: 0.2
                }
              },
              {
                value: 100,
                color: 'transparent'
              }
            ]
          }
        ]
      })
    }
  }
}

</script>
<style>
html,body {
  width: 100%;
  height: 100%;

  padding: 0;
  margin: 0;
}
body {
  background: -webkit-radial-gradient(
    50% 35%,
    farthest-corner,
    #034f8e,
    #034987,
    #02366d,
    #002353
  );
  color: #fff;
  font-size: 16px;
}

</style>
<style scoped lang='scss'>
$border: 1px solid rgba(7, 118, 181, 0.5);
$boxShadow: inset 0 0 20px rgba(7, 118, 181, 0.4);
$fontColor: #00ffff;
$borderBottomColor: rgba(7,118,181,.5);
$radius: 4px;
$statusRadius: 2px;
//添加阴影
.bg-shadow::before {
  width: 100%;
  height: 1px;
  content: "";
  position: absolute;
  left: 0;
  bottom: -1px;
  background: linear-gradient(to right, #076ead, #4ba6e0, #076ead);
  box-shadow: 0 0 5px rgb(131, 189, 227);
  opacity: 0.6;
}
.container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  min-width: 1200px;
  min-height: 860px;
  user-select: none;
  padding: 0 24px;
  box-sizing: border-box;
}
.header {
  height: 54px;
  line-height: 54px;
  margin: 12px 0;
  border: $border;
  box-shadow: $boxShadow;
  font-weight: 600;
  color: #daf9ff;
  padding-left: 33%;
  .title {
    font-size: 24px;
    letter-spacing: 10px;
  }
  .cur-date {
    font-size: 20px;
    float: right;
    padding-right: 12px;
  }
}
.content-wrap {
  display: flex;
  flex: 1;
  padding-bottom: 24px;
  .center-wrap {
    flex: 1;
    min-width: 600px;
    margin: 0 12px;
  }
  .left-wrap, .right-wrap {
    width: 360px;
  }
}

.left-wrap, .right-wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .item-wrap {
    padding: 0 20px;
    border: $border;
    box-shadow: $boxShadow;
    flex: 1;
  }
  .item-wrap:nth-of-type(2) {
    margin: 12px 0;
  }
  /** 
    因为图标和字体的大小不一致，故使用flex布局对齐
   */
  .desc-title {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    border-bottom: 1px solid #cccccc;
    font-size: 16px;
    color: $fontColor;
    border-bottom: 1px solid $borderBottomColor;
    .iconfont {
      font-size: 26px;
    }
  }
  .box-wrap {
    display: flex;
    justify-content: space-between;
    padding: 24px 16px;
    .box {
      box-sizing: border-box;
      width: 128px;
      padding: 28px 20px;
      display: flex;
      flex-direction: column;
      align-items: center;
      background-color: #174d85;
      span {
        display: block;
        text-align: center;
      }
      .data {
        font-size: 32px;
        color: #76ebeb;
      }
      .label {
        font-size: 12px;
        color: #ffffff;
        margin: 12px 0;
      }
      .btn {
        font-size: 14px;
        display: inline-block;
        padding: 2px 16px;
        text-align: center;
        color: #ffffff;
        width: 32px;
        border-radius: $statusRadius;
      }
    }
  }
}

.left-wrap, .right-wrap {
  .item-wrap {
    .data-wrap.box-wrap {
      display: flex;
      flex-direction: column;
      align-items: center;
      .data {
        width: 100%;
        display: flex;
        justify-content: center;
      }
      strong {
        display: inline-block;
        width: 56px;
        height: 56px;
        line-height: 56px;
        text-align: center;
        background-color:#013c72;
        margin-right: 8px;
        padding: 2px 0 2px 3px;
        color: $fontColor;
        font-size: 36px;
      }
      /deep/ .step-wrap {
        margin-top: 24px;
      }
      .desc {
        font-size: 14px;
        display: inline-block;
        padding: 2px 16px;
        text-align: center;
        color: #ffffff;
        width: 32px;
        border-radius: $statusRadius;
        margin-top: 24px;
      }
    }
  }
  .bubble {
    height: calc(100% - 100px);
    display: flex;
    overflow: hidden;
    p.tem {
      width: 70px;
      height: 70px;
      border: 2px solid rgba(5,118,254,1);
      box-shadow: inset 0 0 20px rgba(5,118,254,1);
    }
    p.hum {
      width: 80px;
      height: 80px;
      border: 2px solid rgba(1,187,181,1);
      box-shadow: inset 0 0 20px rgba(1,187,181,1);
      
    }
    p.pm {
      width: 90px;
      height: 90px;
      border: 2px solid rgba(238,255,65,1);  
      box-shadow: inset 0 0 20px rgba(238,255,65,1);
    }
    .data-wrap {
      display: block;
      border-radius: 50%;
      color: #fff;
      font-weight: bold;
      font-size: 12px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }
}

.center-wrap {
  display: flex;
  flex-direction: column;
  .environment-warp {
    height: 116px;
    display: flex;
    justify-content: space-between;
    .environment-param {
      &:first-child, &:last-child {
        margin: 0;
      }
      height: 116px;
      box-sizing: border-box;
      padding:12px;
      border-radius: $radius;
      flex: 1;
      margin: 0 6px;
      span {
        display: block;
        color: #ffffff;
        font-size: 14px;
        line-height: 22px;
      }
      em {
        font-size: 12px;
        font-style: normal;
      }
      .data {
        font-size: 32px;
        line-height: 48px;
      }
    }
  }
  .cur-pm-wrap {
    flex: 1;
    margin: 12px 0;
    border: $border;
    box-shadow: $boxShadow;
    display: flex;
    flex-direction: column;
    .cur-pm-title {
      height: 42px;
      line-height: 42px;
      text-align: center;
      letter-spacing: 2px;
      color: $fontColor;
      font-size: 22px;
    }
    .cur-pm-content {
      flex: 1;
      display: flex;
      #step-chart {
        height: 100%;
        width: 20%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      #cur-pm-chart {
        height: 100%;
        width: 80%;
      }
    }
  }
  .pm-analysis-wrap {
    height: 220px;
    border: $border;
    box-shadow: $boxShadow;
    display: flex;
    flex-direction: column;
    .pm-btn-group {
      font-size: 12px;
      text-align: center;
      height: 32px;
      span {
        margin: 8px 4px 0 4px;
        cursor: pointer;
        display: inline-block;
        padding: 4px 12px;
        transform: scale(0.9);
        border-radius: $radius;
      }
      span.active {
        background-color: #013c72;
        color: $fontColor;
        transform: scale(1.1);
      }
    }
    #pm {
      width: 100%;
      flex: 1;
    }
  }
}

</style>